<template>
        <div class="tools">
        <i class="glyphicon glyphicon-plus" @click="addArticle"></i>
        <i class="glyphicon glyphicon-star" @click="changeFavorite" :class="{like: activeArticle && activeArticle.isF}"></i>
        <i class="glyphicon glyphicon-remove" @click="removeNotes"></i>
    </div>
</template>

<script>
import { mapMutations,mapState } from 'vuex'
export default {
    methods: {
        ...mapMutations(['addArticle','changeFavorite','removeNotes'])
    },
    computed: {
        ...mapState(['activeArticle'])
    }
}
</script>

<style scoped>
    .tools {
        width: 75px;
        height: 100%;
        float: left;
        background-color: aquamarine;
    }

    .tools > i {
        font-size: 35px;
        padding: 10px 20px;
        margin-top: 20px;
        color: white;
    }
    .like {
        color: gold!important;
    }
</style>



