<template>
    <div class="notelist">
        <div class="header">
            <h2>Note-app</h2>
            <div class="btn-group btn-group-justified" role="group" aria-label="...">
                <div class="btn-group" role="group">
                    <button 
                        type="button" 
                        class="btn btn-default" 
                        :class="{active: flag}"
                        @click="flag = true">All Notes</button>
                </div>
                <div class="btn-group" role="group">
                    <button 
                        type="button" 
                        class="btn btn-default" 
                        :class="{active: !flag}"
                        @click="flag = false">Favorites</button>
                </div>
            </div>
        </div>
        <div class="list">
            <div 
                :class="{ now: art.time === activeArticle.time}"
                v-for="(art,index) in flag ? articleList : favoriteNotes" 
                :key="index" 
                @click="changeActive(art) ">{{art.title}}</div>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations,mapGetters } from 'vuex'
export default {
    data() {
        return {
            flag: true
        }
    },
    computed: {
        ...mapState(['articleList','activeArticle']),
        ...mapGetters(['favoriteNotes'])
    },
    methods : {
        ...mapMutations(['changeActive'])
    }
}
</script>

<style scoped>
    .notelist {
        width: 300px;
        height: 100%;
        padding: 10px 20px;
    }
    h2 {
        text-align: center;
    }
    .notelist .header {
        height: 15%;
    }
    .notelist .list {
        height: 83%;
        overflow-y: auto;
    }
    .list > div {
        height: 40px;
        line-height: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .now {
        background-color: blanchedalmond;
    }
</style>


