import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        articleList: [],
        activeArticle: null,
    },
    mutations: {
        addArticle(state) {
            let obj = {
                title : 'None',
                content: ``,
                isF: false,
                time: new Date().getTime()
            }
            state.articleList.push(obj)
            state.activeArticle = obj
        },
        changeActive(state,art) {
            state.activeArticle = art;
        },
        changeFavorite(state) {
            state.activeArticle.isF = !state.activeArticle.isF
        },
        removeNotes (state) {
            if (state.articleList.length !== 0) {
                state.articleList = state.articleList.filter(art => art !== state.activeArticle);
                state.activeArticle = state.articleList[0];
            }
            
        }
    },
    getters: {
        favoriteNotes(state) {
            return state.articleList.filter(art => art.isF)
        }
    }
})


