<template>
    <div class="wrapper">
        <tools></tools>
        <nodelist></nodelist>
        <editor></editor>
    </div>
</template>

<script>
import tools from './components/tools'
import nodelist from './components/nodelist'
import editor from './components/editor'

export default {
    components: {
        tools,
        nodelist,
        editor
    }
}
</script>

<style>
    * {
        padding: 0;
        margin: 0;
        box-sizing: border-box;
    }
    html,body {
        height: 100%;
    }
    .wrapper {
        height: 100%;
        display: flex;
    }
</style>



