<template>
    <div class="editor" v-if="activeArticle">
        <div class="form-group title">
            <label for="title">Title</label>
            <input 
                type="text" 
                class="form-control" 
                id="title" 
                placeholder="input a title"
                v-model="activeArticle.title">
        </div>
        <div class="form-group content">
            <label for="article">Article</label>
            <textarea 
                class="form-control" 
                id="article" 
                rows="28"
                v-model="activeArticle.content"></textarea>
        </div>
        
    </div>    
</template>

<script>
import {mapState} from 'vuex'
export default {
    computed: {
        ...mapState(['activeArticle'])
    }
}
</script>


<style scoped>
    .editor {
        flex-grow: 1;
        height: 100%;
    } 

</style>



